.usersSearch {
  display: flex;
  input {
    padding: 5px 8px 5px 8px;
    height: 40px;
    border-radius: 5px 0 0 5px;
    background-color: var(--input-background-color);
    border: var(--input-border-color) solid 3px;
    border-right: var(--input-border-color) solid 2px;
  }
  .submitButton {
    border: var(--input-border-color) solid 3px;
    border-left: none;
    border-radius: 0 5px 5px 0;
    background-color: var(--input-background-color);
    &:hover {
      background-color: transparent;
    }
    svg {
      height: 40px;
      width: 40px;
    }
  }
}
